
#auth h1 {
    font-size: 55px;
    font-weight: 300;
}

#auth h2 {
    margin: -15px;
    opacity: 0.75;
    font-size: 25px;
    font-weight: 200;
}

.authScreenHeading {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}

.authScreenLogoDiv {
    width: 35%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 20px;
    text-align: center;
}

.authScreenLogoDiv p {
    font-weight: 300;
}

.authScreenLogo {
    opacity: 0.2;
    width: 80%;
    min-width: 350px;
    
}

.authScreenFormsContainer {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 100px;
}

.authScreenFormsDiv {
    padding: 20px;
    width: 65%;
    min-width: 590px;
    display: flex;
    justify-content: center;
}

.authScreenFormsDiv div {
    padding: 10px;
}

.loginForm {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}

.loginForm div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.loginForm h3 {
    font-size: 30px;
    font-weight: 200;
    margin-top: 0;
}

.logFail {
    color: rgb(116, 49, 49);
}

.indexScreenDiv {
    margin-bottom: 100px;
}

.indexContentDiv {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin-top: 25px;
}

.employeeListDiv {
    background-color: rgba(0, 0, 0, 0.089);
    padding: 10px;
    border-radius: 5px;
    width: 65%;
    min-width: 500px;
}

.indexLogoDiv {
    display: flex;
    justify-content: center;
    opacity: 0.4;
    min-width: 200px;
    width: 25%;
    padding: 50px;
}

.indexLogoDiv img {
    width: 100%;
    min-width: 200px;
}

.modalDiv {
    display: flex;
    flex-direction: column;
    /* color: #7008036c; */
}

.indexScreenTitle h2 {
    text-align: center;
    padding: 10px;
    font-size: 20px;
    font-weight: 200;
}

.indexScreenOrg {
    background-image: url('./images/honeycomb_light.jpg');
    background-size: contain;
    background-blend-mode: lighten;
    width: 100%;
    padding: 5px 20px;
    margin-top: 60px;
    border-radius: 20px;
}

.indexScreenOrg h3 {
    font-weight: 400;
}

.table {
    width: 100%;
}

.table th, .table td {
    width: 16%;
    padding: 5px;
}

.tableNameCell {
    width: 20%;
    padding: 5px;
}

.tableButton {
    text-align: center;
}


.columnHeaders {
    margin-left: 30px;
}

.columnHeaders div {
    width: 20%;
    font-size: 10px;
}

.addEmployee {
    font-weight: 200;
}

.addEmployee:hover {
    color: green;
}

#logout:hover {
    color: gray;
    
}

/* for logout on nav */
.MuiToolbar-root {
    justify-content: space-between;
}

@media only screen and (max-width: 600px) {
    html, body {
        width: 100%;
    }

    #auth h1 {
        font-size: 30px;
        margin-bottom: 20px;
        padding-top: 40px;
        padding-bottom: 60px;
        border-bottom: 1px solid rgba(128, 128, 128, 0.267);
    }

    .authScreenHeading h2 {
        display: none;
    }

    .authScreenLogoDiv, .authScreenFormsContainer {
        width: auto;
        padding: 0px;
    }

    .authScreenFormsContainer {
        flex-direction: column-reverse;
    }

    .authScreenFormsDiv {
        min-width: 100%;
        padding: 5px;
        padding-bottom: 20px;
        border-bottom: 1px solid rgba(128, 128, 128, 0.267);
    }

  .authScreenLogo {
        min-width: 120px;
        width: 55%;
        margin-top: 35px;
    }

    .indexScreenOrg {
        margin-top: 15px;
    }

    .indexLogoDiv {
        padding-top: 30px;
    }

    .tableStatus, .tableDOB, .tableHireDate, .authDescription {
        display: none;
    }
    .table {
        margin: 0;
        padding: 0;
    }
    .table, .employeeListDiv, .indexContentDiv {
        max-width: 100%;
        min-width: 100%;
    }

    .makeStyles-modalDeleteButton-225 {
        float: right !important;
    }
  }